








import {
	mapState,
	mapActions
} from "vuex";
export default {
	computed: {
		...mapState(["token"])
	},
	data() {
		return {
			content: ''
		}
	},
	methods:{
		goApply(){
			if (!this.token) {
				uni.showToast({
					title: '请先登录',
					icon: 'none',
					duration: 1500,
					mask: true
				});
				setTimeout(()=>{
					uni.redirectTo({
						url: '/pages/login/login'
					})
				}, 2000)	
				return
			}		
			uni.navigateTo({
				url: '/pages/user/userInfo/userInfo'
			});
		}
	},
	onLoad(option) {
		const ID = option.ID;
		this.API.getDailiProductH5({
			ID
		}).then(res => {
			if(res.data){
				this.content = res.data.description ;
			}
		})
	}
}
